.Feedback__Controls {
  display: block;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 20px;
}

.Btn {
  width: 90px;
  height: 40px;
  background-color: gray;
  font-size: medium;
  font-weight: 600;
  border: 0;
  color: white;
  margin-right: 10px;
}
:hover.Btn {
  border-radius: 3px;
  border: rgb(20, 0, 46) solid 1px;
}

.Good.Btn {
  background-color: green;
}
.Neutral.Btn {
  background-color: orange;
}
.Bad.Btn {
  background-color: red;
}
